  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 18px;
  color: #1f1f1f;
  margin: 0;
}
.card .card-title.card-title-dash.font-weight-medium {
  font-weight: 500;
}
.card .card-subtitle {
  font-weight: 400;
  margin-top: 0.625rem;
  margin-bottom: 0.625rem;
}
.card .card-description {
  margin-bottom: 1rem;
  font-weight: 400;
  color: #76838f;
}
.card.card-outline-success {
  border: 1px solid #34b1aa;
}
